



























import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			mobile: '',
			secret: '',
			logining: false
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toLogin: function() {
			this.$load.loadding('登录中……');
			this.logining = true;
			let token = this.$ajax.getToken();
			uni.request({
				method: 'POST',
				url: this.$ajax.ajaxBaseUrl + '/api/login',
				dataType: 'json',
				data: {
					mobile: this.mobile,
					secret: this.secret
				},
				header: {
					'content-type': 'application/json',
					"Authorization": token.token_type + ' ' + token.access_token
				},
				success: (res) => {
					this.$load.endLoadding();
					
					if (res.data.access_token) {
						uni.setStorageSync('userToken', res.data)
						this.login(res.data);
						uni.navigateBack();
					} else {
						this.$api.msg(res.data.message);
						this.logining = false;
					}
				}
			});
		}
	},

}
